import { Component, OnInit } from "@angular/core";
import { HttpErrorResponse } from "@angular/common/http";
import { CreateQuoteService } from "src/app/core/services/create-quote-service";
import { CommonService } from 'src/app/core/services/common-service';

@Component({
  selector: "app-main-profile",
  templateUrl: "./main-profile.component.html",
  styleUrls: ["./main-profile.component.css"],
})
export class MainProfileComponent implements OnInit {
  token: any;
  filtersLoaded: Promise<boolean>;
  rotateIcon1: boolean = false;
  rotateIcon2: boolean = false;
  rotateIcon3: boolean = false;
  searchParams = {
    imdCode: "BSC5036312",
    token: this.token,
  };

  userDetails: any;

  constructor(private createQuoteService: CreateQuoteService, private commonService: CommonService) {
    this.gettoken();
  }

  ngOnInit() { }

  rotateIcon(iconID: number) {
    switch (iconID) {
      case 1:
        this.rotateIcon1 = !this.rotateIcon1;
        break;
      case 2:
        this.rotateIcon2 = !this.rotateIcon2;
        break;
      case 3:
        this.rotateIcon3 = !this.rotateIcon3;
        break;
    }
  }

  userProfileDetails() {
    this.searchParams.token = this.token;
    this.createQuoteService.userProfileDetails(this.searchParams).subscribe(
      (res) => {
        this.filtersLoaded = Promise.resolve(true);
        this.userDetails = res;
        console.log(this.userDetails);
      },
      (error) => () => { }
    );
  }
  gettoken() {
    this.commonService.getToken().subscribe(
      (res) => {
        this.token = res;
        this.userProfileDetails();
      },
      (error: HttpErrorResponse) => {
        this.token = error.error.text; // body
        this.userProfileDetails();
      }
    );
  }
}
