import { Component, OnInit } from "@angular/core";
import { HttpErrorResponse } from "@angular/common/http/http";
import { CreateQuoteService } from "src/app/core/services/create-quote-service";
import { Router } from "@angular/router";
import { CommonService } from 'src/app/core/services/common-service';

@Component({
  selector: "app-editprodisplay",
  templateUrl: "./editprodisplay.component.html",
  styleUrls: ["./editprodisplay.component.css"],
})
export class EditprodisplayComponent implements OnInit {
  token: any;
  userDetails: any;
  userSaveDetails = {
    alternateemail: "",
    alternatemobilenumber: "9876543211",
    emergencycontactname: "Siva",
    emergencycontactnumber: "1234567890",
    imdCode: "BSC5036312",
    pan: "CRMPK5581S",
    token: this.token,
  };
  rotateIcon1: boolean = false;
  rotateIcon2: boolean = false;
  constructor(
    private createQuoteService: CreateQuoteService, private commonService: CommonService,
    private router: Router
  ) {
    this.gettoken();
  }

  ngOnInit() {}

  rotateIcon(iconID: number) {
    switch (iconID) {
      case 1:
        this.rotateIcon1 = !this.rotateIcon1;
        break;
      case 2:
        this.rotateIcon2 = !this.rotateIcon2;
        break;
    }
  }

  searchParams = {
    imdCode: "BSC5036312",
    token: "29655fbd-b944-464e-8d3d-b9f8830b53e2",
  };

  gettoken() {
    this.commonService.getToken().subscribe(
      (res) => {
        this.token = res;
        this.userProfileDetails();
      },
      (error: HttpErrorResponse) => {
        this.token = error.error.text; // body
        this.userProfileDetails();
      }
    );
  }
  userProfileDetails() {
    this.searchParams.token = this.token;
    this.createQuoteService.userProfileDetails(this.searchParams).subscribe(
      (res) => {
        this.userDetails = res;
        console.log(this.userDetails);
      },
      (error) => () => {}
    );
  }

  saveUserProfileDetails(userdetails: any) {
    this.userSaveDetails.emergencycontactname = this.userDetails.emergencyContactPersonName;
    this.userSaveDetails.token = this.token;
    this.createQuoteService
      .saveUserProfileDetails(this.userSaveDetails)
      .subscribe(
        (res) => {},
        (error) => {
          this.router.navigate(["/my-profile"]);
        }
      );
  }
}
